import React, { Component } from 'react';
import styled from 'styled-components';

const Sidebar = styled.div`
	max-width: 400px;
	background: #1b232e;
	box-shadow: 3px 0px 14px 0px #09101a;
	.maps_header {
		height: 100px;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		&:after {
			content: '';
			width: 80%;
			height: 2px;
			background: #fff;
			position: absolute;
			bottom: 0;
			margin: auto;
		}
		h2 {
			margin: unset;
			color: #fff;
			text-transform: uppercase;
			letter-spacing: 2px;
			font-size: 50px;
		}
	}
	.maps {
		max-height: calc(100vh - 100px);
		overflow: scroll;
		width: 100%;
		padding-left: unset;
		.map {
			cursor: pointer;
			position: relative;
			display: block;
			margin: 13.5px auto;
			border-radius: 5px;
			max-width: 275px;
			box-shadow: 0px 3px 8px black;
			height: 160px;
			img {
				width: 100%;
				height: 100%;
				border-radius: inherit;
			}
			.map_overlay {
				height: 100%;
				width: 100%;
				background: rgba(0, 0, 0, 0.6);
				position: absolute;
				top: 0;
				left: 0;
				border-radius: inherit;
				transition: 500ms ease;
			}
			&:hover {
				.map_overlay {
					background: rgba(0, 0, 0, 0);
				}
			}
		}
	}
`;

class map_selector extends Component {
	render() {
		return (
			<Sidebar>
				<div className="maps_header">
					<h2>Maps</h2>
				</div>
				<ul className="maps">
					{this.props.map_json.map(item => {
						return (
							<li className="map" key={item.map_name}>
								<img src={item.file_path} alt="" />
								<span className="map_overlay" />
							</li>
						);
					})}
				</ul>
			</Sidebar>
		);
	}
}

export default map_selector;
